.Homepage {
font-family: Roboto, sans-serif;
}

header {
position: fixed;
top: 0;
left: 0;
right: 0;
background-color: #32a09e;
padding: 15px 0;
z-index: 1000;
}

nav {
display: flex;
justify-content: center;
flex-wrap: wrap;
}

button {
margin: 5px 30px;
padding: 10px 20px;
font-size: 16px;
background-color: #32a09e;
color: white;
border: none;
cursor: pointer;
transition: background-color 0.3s;
}

button:hover {
background-color: #3abab8;
}

main {
padding-top: 60px;
}

section {
padding: 20px;
box-sizing: border-box;
}

section:nth-child(odd) {
background-color: #f2f2f2;
}

h2 {
font-size: 2em;
margin-bottom: 20px;
text-align: center;
}

.about-section {
display: flex;
flex-direction: column;
align-items: center;
padding: 20px;
}

.about-section {
display: flex;
flex-direction: column;
align-items: center;
padding: 20px;
}

.about-content {
display: flex;
flex-direction: row;
width: 100%;
max-width: 1200px;
gap: 40px;
align-items: flex-start;
}

.about-text {
flex: 2;
padding-right: 20px;
font-size: 22px;
align-items: flex-start;
line-height: 1.2;
}

.about-text br {
content: "";
display: block;
margin-bottom: 1em;
}


.about-image {
flex: 1;
display: flex;
flex-direction: column;
align-items: center;
gap: 20px;
}

.about-image img {
max-width: 300px;
height: auto;
border-radius: 40px;
}

.social-buttons-container {
display: flex;
flex-direction: column;
gap: 10px;
width: 100%;
max-width: 300px;
}

/* Media query for mobile devices */
@media (max-width: 768px) {
.about-content {
    flex-direction: column;
}

.about-text, .about-image {
    width: 100%;
}

.about-image img {
    max-width: 100%;
}
}

.social-buttons-container {
display: flex;
flex-direction: column;
gap: 10px;
width: 100%;
max-width: 300px;
}

.social-button {
display: flex;
align-items: center;
gap: 10px;
padding: 10px 20px;
border-radius: 50px;
background: #000;
border: 2px solid #fff;
cursor: pointer;
transition: all 0.3s ease;
}

.social-buttons-container a {
text-decoration: none;
color: #fff;
font-size: 2rem; /* Adjust size to match image better */
}

.social-button span {
color: #fff;
font-size: 1rem;
}

.linkedin:hover {
background-color: #0077b5;
border-color: #0077b5;
transform: scale(1.02);
}

.github:hover {
background-color: #0077b5;
border-color: #0077b5;
transform: scale(1.02);
}

.projects-section {
padding: 20px;
}

.projects-grid {
display: grid;
grid-template-columns: repeat(3, 1fr); /* 3 columns per row */
gap: 20px;
max-width: 1200px;
margin: 0 auto;
padding: 20px;
}

/* Media query for tablets */
@media (max-width: 1024px) {
.projects-grid {
    grid-template-columns: repeat(2, 1fr); /* 2 columns per row for tablets */
}
}

/* Media query for mobile devices */
@media (max-width: 768px) {
.projects-grid {
    grid-template-columns: 1fr; /* 1 column per row for mobile */
}
}

.project-card {
position: relative;
border-radius: 15px;
overflow: hidden;
transition: transform 0.3s ease;
background: #f2f2f2;
aspect-ratio: 4/3;
}

.project-content {
position: relative;
width: 100%;
height: 100%;
}

.project-content img {
width: 100%;
height: 100%;
object-fit: cover;
}

.project-content h3 {
position: absolute;
bottom: 20px;
left: 20px;
color: #000;
z-index: 1;
}

.project-hover {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
background: rgba(50, 160, 158, 0.95);
color: #000;
display: flex;
flex-direction: column;
align-items: flex-start;
justify-content: flex-start;
padding: 20px;
opacity: 0;
transition: opacity 0.3s ease;
box-sizing: border-box;
}

.project-icons {
display: flex;
flex-direction: row;
gap: 10px;
margin-top: 10px;
}

.project-icons i {
font-size: 2rem;
color: #000;
}

.project-card:hover {
transform: scale(1.05);
}

.project-card:hover .project-hover {
opacity: 1;
}

.cv-section, .contact-section {
padding: 20px;
text-align: center;
}

.cv-content, .contact-content {
display: flex;
justify-content: center;
align-items: center;
width: 100%;
max-width: 600px;
margin: 0 auto;
}

.cv-button, .contact-button {
width: 100%;
background: #000;
margin: 20px 0;
display: flex;
align-items: center;
justify-content: center;
padding: 10px 20px;
border-radius: 50px;
color: #fff;
text-decoration: none;
}

.cv-button:hover, .contact-button:hover {
background-color: #32a09e;
border-color: #32a09e;
transform: scale(1.02);
}

@media (max-width: 768px) {
.about-content {
    flex-direction: column;
}

.about-text, .about-image {
    width: 100%;
    padding: 0;
}

.projects-grid {
    grid-template-columns: 1fr;
}

.project-card {
    aspect-ratio: 3/2;
}

.cv-section, .contact-section {
    padding: 10px;
}

.cv-button, .contact-button {
    padding: 10px;
}
}